import React, { useState, useEffect } from 'react'
import { format } from 'date-fns'

import API from '../api/Api'

import DefaultTemplate from '../components/template/DefaultTemplate'
import Table from '../components/table/Table'

const DISP_CLASS = {
  0: '차단',
  1: 'APP',
  2: 'MMS',
  4: 'DM',
  6: 'DM'
}

const PostingLookup = () => {
  const [data, setData] = useState([])

  const columns = [
    {
      field: 'org_name',
      title: '발송기관'
    },
    {
      field: 'dept_name',
      title: '발송부서'
    },
    {
      field: 'reg_dt',
      title: '발송날짜',
      render: rowData => format(new Date(rowData.reg_dt), 'yyyy-MM-dd HH:mm:dd')
    },
    {
      field: 'ci',
      title: '수신자 CI'
    },
    {
      field: 'name',
      title: '수신자 이름'
    },
    {
      field: 'doc_title',
      title: '문서 제목'
    },
    {
      field: 'read_at',
      title: '열람여부'
    },
    {
      field: 'doc_download_YN',
      title: '다운로드여부'
    },
    {
      field: 'disp_class',
      title: '발송종류',
      render: rowData => DISP_CLASS[rowData.disp_class],
      customFilterAndSearch: (keyword, rowData) => (
        Object.keys(DISP_CLASS).reduce((acc, key) => (
          acc === true || DISP_CLASS[key].includes(keyword) && rowData.disp_class === key
        ), false)
      )
    },
    {
      field: 'disp_status',
      title: '발송상태'
    },
    {
      field: 'request_id',
      title: '발송 신청번호'
    },
    {
      field: 'groupId',
      title: '묶음발송 신청번호'
    },
    {
      field: 'opener',
      title: '우편열람'
    }
  ]

  const init = async () => {
    const res = await API.getPostSend()

    // fetch('http://211.253.86.102:22102/stats/time', {
    //   method: 'POST',
    //   headers: {
    //     'Accept': 'application/json',
    //     'Content-Type': 'application/json'
    //   },
    //   body: JSON.stringify({
    //     "org_code": "52001",
    //     "dept_code": "dept001",
    //     "gte": "2010-12-18T15:00:00",
    //     "lt": "2019-12-20T15:00:00"
    //   })
    // }).then(res => console.log(res))

    setData(res)
  }

  useEffect(() => {
    init()
  }, [])

  return (
    <DefaultTemplate>
      <Table
        scrollable
        term
        termColumn="reg_dt"
        title="우편물 발송 조회"
        columns={columns}
        data={data}
        options={{
          filtering: true,
          exportButton: true
        }}
      />
    </DefaultTemplate>
  )
}

export default PostingLookup