import React, { useState, useEffect } from 'react'

import API from '../api/Api'
import { format } from 'date-fns'
import { withRouter } from 'react-router-dom';

import { Paper, Box, Card, Grid, Typography, Button, ButtonGroup, Icon, IconButton, CardContent } from '@material-ui/core'
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import MenuIcon from '@material-ui/icons/Menu';


const CallView = ({ history, match }) => {
  const [loading, setLoading] = useState(false)
  const [state, setState] = useState({
    class: 0,
    subject: '',
    content: '',
    reg_name: '-',
    reg_dt: '',
  })

  const handleGoList = () => {
    history.push(`/user`)

    return false
  }

  const init = async () => {
    const [ data ] = await API.getNotice(match.params.noticeId)

    setState(data)
    setLoading(true)
  }

  useEffect(() => {
    init()
  }, [])

  return (
    <div style={{ backgroundColor: '#fff', height: '100vh' }}>
      <Box>
        <Box padding={2} display="flex" justifyContent="space-between" alignItems="center" style={{ height: '4em', borderBottom: '1px solid rgba(0,0,0,.1)' }} paddingBottom={2}>
          <Box display="flex" alignItems="center" onClick={handleGoList} style={{ cursor: 'pointer' }}>
            <ChevronLeftIcon></ChevronLeftIcon>
          </Box>
          <Box display="flex" alignItems="center">
            <Typography variant="h5">
              <div>공지사항</div>
            </Typography>
          </Box>
          <Box display="flex" alignItems="center">
            <MenuIcon></MenuIcon>
          </Box>
        </Box>
        <Box padding={2}>
          {loading && (
            <Card variant="outlined">
              <Box padding={2}>
                <Typography variant="h5">
                  {state.subject}
                </Typography>
                <Box marginY={1}>
                  <hr></hr>
                </Box>
                <Typography variant="body1">
                  {state.content}
                </Typography>
                {state.file && (
                  <a href={`http://postalk.gabia.io/api/upload/${state.file}/download`}>
                    첨부파일 다운로드
                  </a>
                )}
                <Box marginTop={2} fontSize="body1.fontSize" textAlign="center">
                  {format(new Date(state.reg_dt), 'yyyy.MM.dd')}
                </Box>
                <Box fontSize="body1.fontSize" textAlign="center">
                  {state.reg_name}
                </Box>
              </Box>
            </Card>
          )}
        </Box>
      </Box>
    </div>
  )
}

export default withRouter(CallView)
