import React from 'react'

import API from '../api/Api'
import useInputs from '../hooks/useInputs'
import { withRouter } from 'react-router-dom';

import { Card, CardContent, Typography, Grid, Box, Button, InputBase, IconButton, TextField } from '@material-ui/core';
import Input from '../components/input/Input'
import DefaultTemplate from '../components/template/DefaultTemplate'
import CloudUpload from '@material-ui/icons/Search';

const NoticeAdd = ({ history }) => {
  const [state, onChange] = useInputs({
    subject: '',
    content: '',
    file: '',
  })

  const handleAdd = async () => {
    if (state.file === '') {
      alert('파일을 업로드해주세요.')
      return false
    }

    const form = new FormData()

    Object.keys({ ...state }).forEach((key, i) => {
      if (key === 'file') {
        form.append(key, state[key][0])
        return true
      }
      form.append(key, state[key])
    })

    const response = await API.addNotice(form)

    if (response.status === 200) {
      alert('공지사항이 등록되었습니다.')
      history.push(`/notice`)
    } else {
      alert('공지사항 등록에 실패했습니다.')
    }
  }

  return (
    <DefaultTemplate>
      <Card>
        <CardContent>
          <Grid container spacing={1}>
            <Grid item xs={12}>
              <TextField
                label="제목"
                name="subject"
                value={state.subject}
                onChange={onChange}
                variant="outlined"
                margin="normal"
                fullWidth
              />
            </Grid>

            <Grid item xs={12}>
              <TextField
                label="내용"
                multiline
                rows={3}
                rowsMax={14}
                name="content"
                value={state.value}
                onChange={onChange}
                variant="outlined"
                margin="normal"
                fullWidth
              />
            </Grid>

            <Grid item xs={12}>
              <TextField
                label="파일첨부"
                name="file"
                type="file"
                onChange={onChange}
                variant="outlined"
                margin="normal"
                InputLabelProps={{ shrink: true }}
                fullWidth
              />
            </Grid>

            <Grid item xs={12}>
              <Box width={1} display="flex" justifyContent="flex-end">
                <Button
                  variant="outlined"
                  color="primary"
                  onClick={handleAdd}
                >확인</Button>
              </Box>
            </Grid>

          </Grid>
        </CardContent>
      </Card>
    </DefaultTemplate>
  )
}

export default withRouter(NoticeAdd)
