import React, { useState } from 'react'
import moment from 'moment'
import { withRouter } from 'react-router-dom';
import { Card, CardContent, Grid, Box, Button, TextField } from '@material-ui/core'

import API from '../api/Api'
import useInputs from '../hooks/useInputs'

import DefaultTemplate from '../components/template/DefaultTemplate'
import DatePicker  from '../components/datePicker/DatePicker'


const OrgAdd = ({ history }) => {
  const [icon, setIcon] = useState(null)

  const [state, onChange] = useInputs({
    org_code: '',
    org_name: '',
    dept_code: '',
    dept_name: '',
    manager: '',
    contact: '',
    email: '',
    validity_dt: (new moment()).format('YYYY-MM-DD'),
    channel: '',
    org: '',
  })

  const [validity, setValidity] = useState(new Date())

  const onChangeValidity_dt = (date) => {
    setValidity(date)
    onChange({
      target: {
        name: 'validity_dt',
        value: (new moment(date)).format('YYYY-MM-DD')
      }
    })
  }

  const handleAdd = async () => {
    if (!codeRegexp.test(state.org_code) || !codeRegexp.test(state.dept_code)) {
      alert('코드는 코드형식(영문소문자 + 숫자 10자 미만)에 맞게 작성해주세요.')
      return false
    }

    const form = new FormData()
    
    Object.keys({...state}).forEach((key, i) => {
      form.append(key, state[key])
    })

    form.append('icon', icon)
    
    const response = await API.addOrg(form)

    if (response.status === 200) {
      alert('발송기관이 등록되었습니다.')
      history.push(`/org`)
    } else {
      alert('발송기관 등록에 실패했습니다.')
    }

  }

  const codeRegexp = new RegExp(/^(?=.*\d)(?=.*[a-z]).{0,9}$/)

  return (
    <DefaultTemplate>
      <Card>
        <CardContent>
          <Grid container spacing={3}>
            <Grid item xs={6}>
              <TextField
                variant="outlined"
                error={!codeRegexp.test(state.org_code)}
                name="org_code"
                value={state.org_code}
                onChange={onChange}
                fullWidth
                label="기관코드"
                helperText="영문소문자 + 숫자 10자 미만"
              />
            </Grid>
            <Grid item xs={6}>
              <TextField
                variant="outlined"
                label="기관명"
                name="org_name"
                value={state.org_name}
                onChange={onChange}
                fullWidth
              />
            </Grid>
            
            <Grid item xs={6} style={{paddingTop: '0px'}}>
              <TextField
                variant="outlined"
                error={!codeRegexp.test(state.dept_code)}
                label="부서코드"
                helperText="영문소문자 + 숫자 10자 미만"
                name="dept_code"
                value={state.dept_code}
                onChange={onChange}
                fullWidth
              />
            </Grid>
            <Grid item xs={6} style={{paddingTop: '0px'}}>
              <TextField
                variant="outlined"
                label="부서명"
                name="dept_name"
                value={state.dept_name}
                onChange={onChange}
                fullWidth
              />
            </Grid>

            <Grid item xs={6}>
              <DatePicker
                label="청약 만료일"
                value={validity}
                onChange={onChangeValidity_dt}
                minDateMessage={`오늘부터 선택해주세요.`}
                TextFieldComponent={(props) => <TextField {...props} fullWidth />}
                disablePast
              />
            </Grid>

            <Grid item xs={6}>
              <TextField
                variant="outlined"
                type="file"
                name="icon"
                label="아이콘 파일"
                InputLabelProps={{ shrink: true }}
                onChange={(e) => setIcon(e.target.files[0])}
                fullWidth
              />
            </Grid>

            <Grid item xs={12}>
              <TextField
                variant="outlined"
                label="담당자"
                name="manager"
                value={state.manager}
                onChange={onChange}
                fullWidth
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                label="연락처"
                name="contact"
                value={state.contact}
                onChange={onChange}
                fullWidth
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                label="email"
                name="email"
                value={state.email}
                onChange={onChange}
                fullWidth
              />
            </Grid>
            <Grid item xs={12}>
              <Box
                width={1}
                display="flex"
                justifyContent="flex-end"
              >
                <Button
                  variant="outlined"
                  color="primary"
                  onClick={handleAdd}
                >등록</Button>
              </Box>
            </Grid>
          </Grid>
        </CardContent>
      </Card>
    </DefaultTemplate>
  )
}

export default withRouter(OrgAdd)
