import React, { useEffect, useRef, useState } from 'react'

import Table from '../components/table/Table'
import { Paper, Box, Card, Grid, Typography, Button, ButtonGroup, Icon, IconButton, CardContent } from '@material-ui/core'
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ExpandMore from '@material-ui/icons/ExpandMore';
import ExpandLess from '@material-ui/icons/ExpandLess';
import MenuIcon from '@material-ui/icons/Menu';

import API from '../api/Api';
import { format } from 'date-fns'
import { withRouter } from 'react-router-dom';

import '../style/main.scss';

const Index = ({ history }) => {
  const [data, setData] = useState([])
  const [active, setActive] = useState(-1)

  const columns = [
    {
      field: 'id',
      title: '번호'
    },
    {
      field: 'subject',
      title: '제목'
    },
    {
      field: 'reg_dt',
      title: '등록일',
      render: rowData => format(new Date(rowData.reg_dt), 'yyyy-MM')
    },
  ]

  const init = async () => {
    const data = await API.getNotices()
    console.log(data)    
    setData(data)
  }

  const handleRowClick = (id) => {
    return () => {
      history.push(`/user/notice/view/${id}`)
    }
  }

  useEffect(() => {
    init()
  }, [])

  return (
    <div style={{ height: '100vh' }} className="userMainPage">
      <Box>
        <Box padding={2} display="flex" justifyContent="space-between" alignItems="center" style={{ height: '4em', borderBottom: '1px solid rgba(0,0,0,.1)' }} paddingBottom={2}>
          <Box display="flex" alignItems="center">
            <ChevronLeftIcon></ChevronLeftIcon>
            <Typography variant="h5">
              <div className="h4">공지사항</div>
            </Typography>
          </Box>
          {/* <Box display="flex" alignItems="center">
            <Typography variant="h5">
              <div>공지사항</div>
            </Typography>
          </Box> */}
          {/* <Box display="flex" alignItems="center">
            <MenuIcon></MenuIcon>
          </Box> */}
        </Box>
        <Box>
          <Card>
            {(data.length > 0) && data.map((x, i) => (
              <Box
                key={i}
                /* onClick={handleRowClick(x.id)} */
                onClick={() => {
                  setActive(active === i ? -1 : i)
                }}
                style={{ cursor: 'pointer' }}
                padding={1}
                display="flex"
                justifyContent="space-between"
                alignItems="center"
              >
                <div className="listItem">
                  <div className="content">
                    <Box>
                      <p>{x.subject}</p>
                      <div className="date">{x.reg_dt.split('T').join(' ').replace('.000Z', '')}</div>
                    </Box>
                    <Box display="flex" alignItems="center">
                      {active === i ? <ExpandLess/> : <ExpandMore />}
                    </Box>
                  </div>
                  {active === i ? (
                    <div className="moreContent">
                      <div dangerouslySetInnerHTML={{__html:x.content}}>
                      </div>
                      {/* {(x.file && x.file !== 'null') ? (
                        <p style={{marginTop: '10px'}}>
                          <a href={window.location.origin + `/api/upload/${x.file}/download`}>파일다운</a>
                        </p>
                      ) : ''} */}
                    </div>
                  ) : ''}
                </div>
              </Box>
            )) }
          </Card>
        </Box>
      </Box>
    </div>
  )
}

export default withRouter(Index) 